import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { DadosMigracaotrabalhoPage } from './dados-migracaotrabalho/dados-migracaotrabalho';
import { DadosCheckpointingPage } from './dados-checkpointing/dados-checkpointing';
import { DadosMigracaopreemptivaPage } from './dados-migracaopreemptiva/dados-migracaopreemptiva';
import { DadosAutocuraPage } from './dados-autocura/dados-autocura';
import { DadosReplicacaoPage } from './dados-replicacao/dados-replicacao';



@Component({
  selector: 'page-armazenamento',
  templateUrl: 'armazenamento.html',
})
export class ArmazenamentoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    
  }

  checkpointing(): void{
    this.navCtrl.push(DadosCheckpointingPage);
  }
  migracaoPreemptiva(): void{
    this.navCtrl.push(DadosMigracaopreemptivaPage);
  }
  migracaoTrabalho(): void{
    this.navCtrl.push(DadosMigracaotrabalhoPage);
  }
  autocura(): void{
    this.navCtrl.push(DadosAutocuraPage);
  }
  replicacao(): void{
    this.navCtrl.push(DadosReplicacaoPage);
  }
}



