import { Component } from '@angular/core';
import {  NavController, NavParams } from 'ionic-angular';
import { ComunicacaoCheckpointingPage } from './comunicacao-checkpointing/comunicacao-checkpointing';
import { ComunicacaoMigracaopreemptivaPage } from './comunicacao-migracaopreemptiva/comunicacao-migracaopreemptiva';
import { ComunicacaoAutocuraPage } from './comunicacao-autocura/comunicacao-autocura';
import { ComunicacaoReplicacaoPage } from './comunicacao-replicacao/comunicacao-replicacao';


@Component({
  selector: 'page-comunicacao',
  templateUrl: 'comunicacao.html',
})
export class ComunicacaoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    
  }

  checkpointing(): void{
    this.navCtrl.push(ComunicacaoCheckpointingPage);
  }
  migracaoPreemptiva(): void{
    this.navCtrl.push(ComunicacaoMigracaopreemptivaPage);
  }
  autocura(): void{
    this.navCtrl.push(ComunicacaoAutocuraPage);
  }
  replicacao(): void{
    this.navCtrl.push(ComunicacaoReplicacaoPage);
  }
}



