import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';


import { VirtualizacaoPage } from '../virtualizacao/virtualizacao';
import { ProcessoPage } from '../processo/processo';
import { ComunicacaoPage } from '../comunicacao/comunicacao';
import { SobrePage } from '../sobre/sobre';
import { ArmazenamentoPage } from '../armazenamento/armazenamento';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController) {

  }


  virtualizacao(): void {

    this.navCtrl.push(VirtualizacaoPage);

  }
  armazenamento(): void{

    this.navCtrl.push(ArmazenamentoPage);
  }

  comunicacao(): void{
    this.navCtrl.push(ComunicacaoPage);
  }

  processo(): void{
    this.navCtrl.push(ProcessoPage);
  }

  sobre(): void {
    this.navCtrl.push(SobrePage);
  }

}



