import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { VirtualizacaoCheckpointingPage } from './virtualizacao-checkpointing/virtualizacao-checkpointing';
import { VirtualizacaoMigracaopreemptivaPage } from './virtualizacao-migracaopreemptiva/virtualizacao-migracaopreemptiva';
import { VirtualizacaoAutocuraPage } from './virtualizacao-autocura/virtualizacao-autocura';
import { VirtualizacaoReplicacaoPage } from './virtualizacao-replicacao/virtualizacao-replicacao';


@Component({
  selector: 'page-virtualizacao',
  templateUrl: 'virtualizacao.html',
})
export class VirtualizacaoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    
  }

  
  checkpointing(): void{
    this.navCtrl.push(VirtualizacaoCheckpointingPage);
  }
  migracaoPreemptiva(): void{
    this.navCtrl.push(VirtualizacaoMigracaopreemptivaPage);
  }
  autocura(): void{
    this.navCtrl.push(VirtualizacaoAutocuraPage);
  }
  replicacao(): void{
    this.navCtrl.push(VirtualizacaoReplicacaoPage);
  }
}



