import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { ProcessoAutocuraPage } from './processo-autocura/processo-autocura';
import { ProcessoMigracaotrabalhoPage } from './processo-migracaotrabalho/processo-migracaotrabalho';
import { ProcessoCheckpointingPage } from './processo-checkpointing/processo-checkpointing';
import { ProcessoReplicacaoPage } from './processo-replicacao/processo-replicacao';

@Component({
  selector: 'page-processo',
  templateUrl: 'processo.html',
})
export class ProcessoPage {

  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    
  }

  
  checkpointing(): void{
    this.navCtrl.push(ProcessoCheckpointingPage);
  }
  migracaoTrabalho(): void{
    this.navCtrl.push(ProcessoMigracaotrabalhoPage);
  }
  autocura(): void{
    this.navCtrl.push(ProcessoAutocuraPage);
  }
  replicacao(): void{
    this.navCtrl.push(ProcessoReplicacaoPage);
  }
}



